const { regClass, property,SoundManager } = Laya;

@regClass('f4a9ed67-7b7a-43be-945e-88ba9965a9d4', '../src/ClickSound.ts')
export class Script extends Laya.Script {

    constructor() {
        super();
    }

    onMouseClick(): void {
        
        SoundManager.playSound("sounds/click.mp3", 1);
    }
}